.hero-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
}

.hero {
  max-width: 500px;
  margin: 1rem;
}

.hero-sub-heading {
  color: #fff;
  margin: 1rem;
  font-size: 1rem;
  letter-spacing: 2px;
  font-family: var(--unnamed-font-family-bahnschrift);
  /* font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold) var(--unnamed-font-size-32)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-bahnschrift); */
}

.action-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.action-btn {
  flex: 1 0 0;
  background: var(--unnamed-color-ffffff) 0% 0% no-repeat padding-box;
  border: 1px solid var(--unnamed-color-ffffff);
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 1px solid #ffffff;
  opacity: 1;
  font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-bold)
    var(--unnamed-font-size-24) / var(--unnamed-line-spacing-34)
    var(--unnamed-font-family-bahnschrift);
  letter-spacing: var(--unnamed-character-spacing-4-8);
  color: var(--unnamed-color-000000);
  text-align: center;
  letter-spacing: 4.2px;
  color: #000000;
  margin: 2rem 3rem;
  padding: 14px;
  border-radius: 2px;
  white-space: nowrap;
}
